@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

body {
	font-size: 1.1rem;
	margin: 0;
	height: 100vh;
	font-family: 'Lato', sans-serif;
	background: #c0c0aa;
	background: -webkit-linear-gradient(to right, #1cefff, #c0c0aa);
	background: linear-gradient(to right, #1cefff, #c0c0aa);
}

#root{
	display: flex;
	justify-content: center;
}

.App {
	display: flex;
	justify-content: center;
}

.TodoList {
	color: white;
	margin-top: 50px;
	background-color: rgb(245, 74, 74);
	box-shadow: -16px -14px 3px 1px rgba(0, 0, 0, 0.16);
}

.TodoList-Header {
	margin: 10px 35px;
}

.TodoList-Header h2 {
	margin-bottom: 0;
}

.TodoList-Header p {
	margin-top: 0;
	margin-bottom: 0;
}

.TodoList-Header hr {
	border: 0;
	margin-bottom: 30px;
	height: 0;
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}

.Todo {
	background-color: rgba(255, 255, 255, 0.3);
	margin: 2px 0;
	padding: 10px 25px;
	display: flex;
	justify-content: space-between;
}

.Todo input {
	padding: 5px;
}

.Todo form button {
	padding: 6px 10px;
	color: white;
	background-color: rgb(245, 74, 74);
	border: 1px solid white;
}

.Todo form button:hover {
	background-color: rgb(185, 57, 57);
	cursor: pointer;
}

.Todo span button {
	background-color: rgba(0, 0, 0, 0);
	color: white;
	border: none;
	font-size: 20px;
	transition: all 0.5 ease-in-out;
}

.Todo span button:focus {
	outline: 0;
}

.Todo span button:hover {
	cursor: pointer;
	transform: scale(1.1);
}

.Todo-Form {
	margin: 30px;
}

.Todo-Form input {
	margin-top: 2px;
	margin-right: 2px;
	padding: 5px;
	min-width: 100px;
}

.Todo-Form button {
	padding: 6px 10px;
	color: white;
	background-color: rgb(245, 74, 74);
	border: 1px solid white;
}

.Todo-Form button:hover {
	background-color: rgb(185, 57, 57);
	cursor: pointer;
}

@keyframes hecho {
	0% {
		width: 0;
	}
	100% {
		width: 100%;
	}
}
.hecho {
	position: relative;
}
.hecho::after {
	content: ' ';
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 1px;
	background: white;
	animation-name: hecho;
	animation-duration: 0.2s;
	animation-timing-function: linear;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}
